import React from 'react';

const Footer = ({social}) => {
    return (
        <footer className="footer">
            <div className="footer__copyright" href="https://sanjaygoraniya.github.io">
                <div className="top">
                    <span>Designed &amp; Built by&nbsp;</span>
                </div>
                <div className="bottom">
                    <span>Sanjay Goraniya</span>
                </div>
            </div>
            <div className="footer__links">
                {social && social.map((link, index) => (
                    <a className="underline-link" target="_blank" rel="noreferrer" key={index} title={link.title} href={link.url}>
                        <span className="text">{link.title}</span>
                        <img className="icon" src={`https://raw.githubusercontent.com/edent/SuperTinyIcons/master/images/svg/${link.title.toLowerCase()}.svg`} alt={link.title} title={link.title} />
                    </a>
                ))}
            </div>
            {/* <div id="google_translate_element"></div> */}
        </footer>
    );
}

export default Footer;