import React from 'react';
import { useData } from '../contexts/index';
import { Head, Navbar, Footer, TopButton } from './';

const Layout = ({children}) => {
    const { site } = useData();

    return (
        <>
            <Head siteMetadata={site.siteMetadata} />
            <Navbar navigation={site.data.navigation} resume={site.data.resume} />
            {children ? children : null}
            <Footer social={site.data.social} />
            <TopButton />
        </>
    )
}

export default Layout;