import React from 'react';
import { Page } from './';
import { Link } from 'react-router-dom';
// import * as yaml from 'yaml-front-matter';
// import truncate from 'lodash.truncate';


const Topic = (props) => {
  // const [topic, setTopic] = React.useState({});
  // React.useEffect(() => {
  //   fetch("https://raw.githubusercontent.com/github/explore/main/topics/"+props.match.params.topic+"/index.md")
  //   .then(async (res) => {
  //     const contents = await res.text();
  //     console.log("RES", contents);
  //     const _metadata = yaml.loadFront(contents);
  //     _metadata.preview = _metadata['__content'].trim();
  //     delete _metadata['__content'];
  //     console.log("_META", _metadata);
  //     setTopic(_metadata);
  //   })
  //   .catch((error) => {
  //     console.log("ERROR", error)
  //   })
  // }, []);

  const topics = [
    { 
      aliases: ['js', 'javascript', 'es', 'es6', 'ecmascript'],
      name: "JavaScript", 
      createdBy: "Brendan Eich", 
      released: "December 4, 1995",
      website: "https://developer.mozilla.org/en-US/docs/Web/JavaScript",
      wikipedia: "https://en.wikipedia.org/wiki/JavaScript",
      logo: "https://raw.githubusercontent.com/github/explore/80688e429a7d4ef2fca1e82350fe8e3517d3494d/topics/javascript/javascript.png",
      description: "JavaScript (JS) is a lightweight interpreted or JIT-compiled programming language with first-class functions. While it is most well-known as the scripting language for Web pages, many non-browser environments also use it, such as Node.js, Apache CouchDB and Adobe Acrobat. JavaScript is a prototype-based, multi-paradigm, dynamic language, supporting object-oriented, imperative, and declarative (e.g. functional programming) styles.",
      related: ['nodejs']
    }
  ]
  const topic = topics.find((topic) => topic.aliases.includes(props.match.params.topic.toLowerCase())) || topics.javascript;
  
  if(!topic) {
    return (
      <Page title={props.match.params.topic.toLowerCase()}>
        <section className="section topics">
          <div className="section__content">
            <div className="topic__title">
              <div className="topic__title__tag">
                #
              </div>
              <h1>{props.match.params.topic.toLowerCase()}</h1>
            </div>
            <div className="topic__not__found">
              The <strong>{props.match.params.topic.toLowerCase()}</strong> topic hasn't been added in the list, yet.
            </div>
            <Link className="email-link" rel="noreferrer" to="/topics">Explore topics</Link>
          </div>
        </section>
      </Page>  
    )
  }
  return (
    <Page title={"Topic : "+topic.name.toLowerCase()}>
      <section className="section topics">
        <div className="section__content">
          <div className="topic__title">
            <div className="topic__title__tag">
              #
            </div>
            <h1>{topic.name}</h1>
          </div>

          <div className="topic__details">
            <p className="createdby__wrapper">
              Created by <span className="createdby">{topic.createdBy}</span> 
            </p>
            <p className="createdby__wrapper">
              Released <span className="createdby">{topic.released}</span> 
            </p>

            {!!topic.url && <a href={topic.url}>
              Website
            </a>}
            {!!topic.wikipedia && <>
              <br />
              <a href={topic.wikipedia}>
                Wikipedia
              </a>
            </>}
          </div>

          <div className="topic__description">
            {!!topic.logo && <div className="topic__img">
              <img src={topic.logo} width="100" height="100" alt={`${topic.name} logo`} />
            </div>}
            <div className="description__wrapper">
              <p>{topic.description}</p>
            </div>
          </div>
          {!!topic?.related?.length && <div className="related__topics">
            <h2>
              Related Topics
            </h2>
            {topic.related.map((relatedItem) => {
              return <a key={relatedItem} title={"Topic: "+relatedItem} href={"/topics/"+relatedItem} className="topic__tag">
                {relatedItem}
              </a>
            })}
          </div>}
        </div>
      </section>
    </Page>
  )
}

export default Topic;