import React from 'react';
import PropTypes from 'prop-types';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const Head = ({ title, description, image, siteMetadata }) => {
    const { pathname } = useLocation();
    
    const {
        title: defaultTitle,
        description: defaultDescription,
        siteUrl,
        image: defaultImage,
        twitterUsername,
    } = siteMetadata;

    const seo = {
        title: title || defaultTitle,
        description: description || defaultDescription,
        image: `${siteUrl}${image || defaultImage}`,
        url: `${siteUrl}${pathname}`,
    };

    return (
        <Helmet title={title} defaultTitle={seo.title} titleTemplate={`%s | ${defaultTitle}`}>
            <html lang="en" />

            <meta name="description" content={seo.description} />
            <meta name="image" content={seo.image} />
            
            <meta name="theme-color" content="#444452" />
            <meta name="color-scheme" content="light dark" />

            <meta property="og:title" content={seo.title} />
            <meta property="og:description" content={seo.description} />
            <meta property="og:image" content={seo.image} />
            <meta property="og:url" content={seo.url} />
            <meta property="og:type" content="website" />

            <meta name="twitter:card" content="summary_large_image" />
            <meta name="twitter:creator" content={twitterUsername} />
            <meta name="twitter:title" content={seo.title} />
            <meta name="twitter:description" content={seo.description} />
            <meta name="twitter:image" content={seo.image} />

            <meta name="hostname" content="sanjaygoraniya.github.io" />
            <meta name="expected-hostname" content="sanjaygoraniya.github.io" />

            <meta name="google-site-verification" content="IZHgAsFUeyA4eW6dyR23PtvXdMa08B-a-sanvrWy8zA" />
        </Helmet>
    );
};

export default Head;

Head.propTypes = {
    title: PropTypes.string,
    description: PropTypes.string,
    image: PropTypes.string,
};

Head.defaultProps = {
    title: null,
    description: null,
    image: null,
};