import { setAttributes } from './';

/**
 * @name handleExternalLinks
 * @description  - Sets target="_blank" and rel="noopener noreferrer" for all external links available in current document.
 * @return {Boolean} - return true if any element change else false.
 */

const handleExternalLinks = () => {
    const links = Array.from(document.querySelectorAll('a'));
    const attributes = {rel: 'noopener noreferrer', target: '_blank'};
    if (links && links.length > 0) {
        links.forEach((link) => link.host !== window.location.host && setAttributes(link, attributes));
        return true;
    }
    return false;
};

export default handleExternalLinks;
