/**
 * @name setAttributes
 * @description - Sets multiple given attributes for given element.
 * @param {DOMElement} element - document element to set attributes.
 * @param {Object} attributes - list of attributes to set for given element.
 * @return {none}
 */

const setAttributes = (element, attributes) => {
  for (let attribute in attributes) {
    element.setAttribute(attribute, attributes[attribute]);
  }
}

export default setAttributes;
