import React, { useEffect, useState } from 'react';

const Span = (props) => {
    const { children, fadeIn = true,className = "", index, ...others } = props;
    const [cls, setCls] = useState("");
    const [style, setStyle] = useState(fadeIn ? {opacity: 0} : {});
    // const [cls, setCls] = useState(className);
    
    useEffect(() => {
        const time = 100 * index;
        const timeOut = setTimeout(() => {
            setCls(` animate`);
            setStyle({});
            // setCls(`${className} animate`);
        }, time);
        return () => {
            clearTimeout(timeOut);
        }
    }, [index, className]);

    const handleAnimEnd = () => {
        setCls('');
    }
    const handleHover = () => {
        setCls(' animate')
    }

    return <span className={`${className}${cls}`} {...others} style={style} onMouseOver={handleHover} onAnimationEnd={handleAnimEnd}>{children}</span>;
}

const RubberText = (props) => {
    const { text, fadeIn, component, ...others } = props;
    const cls = "rubber-text";
    const CustomTag = component || 'h1';

    return(
        <CustomTag aria-label={text} {...others}>
            {text.split('').map((char, index) => (<Span className={cls} key={index} index={index} aria-hidden="true" fadeIn={fadeIn}>{char}</Span>))}
        </CustomTag>
    );
}

export default RubberText;