import React from 'react';
import { Link } from 'react-router-dom';
import { BlogDetails } from './';

const BlogCard = ({blog}) => {
    const { slug, title, date:d, 
        // content, readtime, 
        author, tags } = blog;
    const date = new Date(d).toLocaleDateString('en-US');
    return (
        <article className="blog__card">
            <BlogDetails {...{date: date, author, readtime: "9 min read"}} />
            <Link className="blog__link" to={`/blog/${slug}`}>{title}</Link>
            {/* <p>{content}</p> */}
            <div className="blog__tags">
                {tags && tags.map((tag, index) => (
                    <a key={index} className="blog__tag" href={`/blog/tags/${tag}`}>{tag}</a>
                ))}
            </div>
        </article>
    )
}

export default BlogCard;