import React, { 
    useState, 
    useEffect 
} from 'react';

const Wave = () => {
    // const [wave, setWave] = useState(true);
    const [wave, setWave] = useState("emoji wave-hand animated");
    
    useEffect(() => {
        const waveTimeOut = setTimeout(() => setWave("emoji wave-hand animated wave"), 1000);
        return () => clearTimeout(waveTimeOut);
    }, []);

    useEffect(() => {
        const waveTimeOut = setTimeout(() => setWave("emoji wave-hand animated"), 1500);
        return () => clearTimeout(waveTimeOut);
    }, [wave]);

    const handleMouseOver = () => {
        // setWave(true)
        setWave("emoji wave-hand animated wave");
    }

    // const handleMouseOut = () => {
    //     // setWave(false)
    //     setWave("emoji wave-hand animated");
    // };

    return (
        <span 
            className={wave}
            onMouseOver={handleMouseOver}
            // onMouseOut={handleMouseOut}
        ></span>
    );
}

export default Wave;