import React, { useState, useEffect } from 'react'

export default function TopButton() {
    const [visible, setVisible] = useState(false);
    
    useEffect(() => {
        let handleScroll = () => {
            toggleVisibility();
            
        }
        document.addEventListener("scroll", handleScroll);
        return () => {
            document.removeEventListener("scroll", handleScroll);
        }
    });

    const toggleVisibility = () => {
        // console.log('topButton: ',window.pageYOffset);
        setVisible(window.pageYOffset > 300);
    }

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    }
    return (
        <>
        {/* {visible ?  */}
            <button id="top-button" className={visible ? 'fadeIn' : 'fadeOut'} onClick={scrollToTop}>
                <img src={process.env.PUBLIC_URL + `/img/emojis/pointing-up.png`} alt="Scroll Top" />
            </button>
            {/* : null} */}
        </>
        
    );
}
