import React, { useEffect } from 'react';
// import RubberText from './RubberText';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const NotFound = () => {

    useEffect(() => {
        document.body.classList.add("not-found");
        return () => {
            document.body.classList.remove("not-found");
        }
    });

    return (
        <section className="not-found-content">
            <Helmet title="Page Not Found" />
            {/* <RubberText className="name" component="a" href="/" text={"Sanjay Goraniya"} /> */}
            <div>
                <h1>404</h1>
                <h4>Looks like you're looking for a page that doesn't exist.</h4>
            </div>
            <Link to="/" className="email-link" rel="noreferrer">
                Home Page
            </Link>
        </section>
    );
}

export default NotFound;