import React from 'react';
import { SectionTitle, IconLink } from './';

const Experience = ({experience}) => {
    return (
        <section id="experience" className="section experience">
            <SectionTitle title="Experience" />
            <div className="section__content">
                <div className="jobs">
                    {experience && experience.map((job, index) => (
                        <div className="job" key={index}>
                            <div className="time-place">
                                <div className="job__company">
                                    <div className="company-name">
                                        <strong>{job.company}</strong>
                                    </div>
                                    <div className="company-links">
                                        <a className="company-link" target="_blank" rel="noreferrer" title={"Website"} href={job.url} ><IconLink icon="Website" /></a>
                                    </div>
                                </div>
                                <div className="job__time">{job.time}</div>
                            </div>
                            <div className="job__position">{job.position}</div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
export default Experience;