import React, { useState, useEffect } from 'react';
import { Switch } from './';
import { Link } from 'react-router-dom';

var lastScroll = 0;

const LogoIcon = () => (
    <svg xmlns="http://www.w3.org/2000/svg" width="2500" height="2500" viewBox="0 0 192.756 192.756">
        <g fillRule="evenodd" clipRule="evenodd">
            <path id="bg" fill="var(--main-background)" d="M182.814 96.378c0-47.713-38.721-86.434-86.435-86.434-47.713 0-86.438 38.721-86.438 86.434 0 47.71 38.724 86.435 86.438 86.435 47.715-.001 86.435-38.725 86.435-86.435z" />
            <path id="ch" fill="var(--link-text)" d="M41.123 116.506c14.406 18.729 36.012 30.732 54.264 30.732 12.002 0 22.567-7.199 22.567-14.885 0-8.164-3.842-13.443-21.606-16.328-33.615-6.723-56.663-26.413-56.663-47.538 0-24.97 23.048-43.695 56.663-43.695 10.084 0 22.569 2.879 48.5 18.244v25.932c-20.648-18.248-35.535-25.932-48.5-25.932-18.248 0-24.012 5.764-24.012 13.926 0 7.684 8.642 14.886 24.012 18.725 44.174 12.487 56.661 22.571 56.661 45.624 0 23.527-25.928 45.617-56.661 45.617-21.128 0-33.138-3.361-55.225-19.689v-30.733z" />
        </g>
    </svg>
);

const Navbar = ({ resume, navigation }) => {
    const [direction, setDirection] = useState('');
    const [hamBoxOpen, setHamBoxOpen] = useState(false);

    useEffect(() => {
        let handleScroll = () => {
            var st = window.pageYOffset || document.documentElement.scrollTop;
            if (st > lastScroll) { // down
                document.querySelector("#top-button").classList.remove("md");
                setDirection("down");
            } else { // up
                if (st < 80) {
                    setDirection("top");
                } else {
                    document.querySelector("#top-button").classList.add("md");
                    setDirection("up");
                }
            }
            lastScroll = (st <= 0) ? 0 : st;
            setHamBoxOpen(false);
        }
        document.addEventListener("scroll", handleScroll, false);
        return () => {
            document.removeEventListener("scroll", handleScroll, false);
        }
    }, []);

    const handleHamBoxClick = () => {
        setHamBoxOpen(pre => !pre);
    }

    return (
        <header className={`navbar ${direction}`}>
            <nav>
                <div className="logo" tabIndex="-1">
                    <a href="/" to="/" aria-label="home">
                        <LogoIcon />
                    </a>
                </div>
                <div className="navbar__links">
                    <ul className={`${hamBoxOpen ? 'show' : ''}`}>
                        {navigation.map((nav, index) => (
                            <li key={index}>
                                {nav.to ? <Link to={nav.to} title={nav.title}>{nav.name}</Link> : <a href={(nav.origin ? window.location.origin : '') + nav.href} title={nav.title}>{nav.name}</a>}
                            </li>
                        ))}
                    </ul>
                    <div className="navbar__link__button">
                        <a href={window.location.origin + resume.link} title={resume.title} className="link__button">{resume.name}</a>
                        <div className="link__button">
                            <Switch />
                        </div>
                        <div className="hamburger__wrapper link__button">
                            <button className={`hamburger__button ${hamBoxOpen ? 'hamburger__close' : 'hamburger__open'}`} onClick={handleHamBoxClick}>
                                <div className="ham-box">
                                    <div className="ham-box-inner"></div>
                                </div>
                            </button>
                        </div>
                    </div>
                </div>
            </nav>
        </header>
    )
}

export default Navbar;