import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import BlogDetails from './BlogDetails';

const SingleBlog = ({ blog }) => {
    const [markdown, setMarkdown] = useState();
    const { slug, title, date: d, 
        // readtime, 
        author, tags } = blog;
    const date = new Date(d).toLocaleDateString('en-US');

    useEffect(() => {
        const fetchData = async () => {
            try {
                let url = `${process.env.PUBLIC_URL}/blogs/${slug}/index.md`;
                let result = await fetch(url);
                let body = await result.text();
                setMarkdown(body);
            } catch (err) {
                console.log("Error in SingleBlog component", err);
            }
        }
        fetchData();
    }, [slug]);

    useEffect(() => {
        const codeEls = Array.from(document.querySelectorAll('pre>code'));
        codeEls.forEach((codeEl) => { codeEl.dataset.lang = codeEl.classList[0].split('-')[1]; });
    });

    const calcReadTime = ev => {
        if(!ev) return
        const wordsPerMinute = 200; // Average case.
        let textLength = ev && ev.split(' ').length; // Split by words
        if (textLength > 0) {
            return `${Math.ceil(textLength / wordsPerMinute)} min read`;
        }
    };

    return (
        <section className="section blog">
            <BlogDetails {...{date: date, author, readtime: calcReadTime(markdown)}} />
            <h1 className="blog__title">{title}</h1>
            <div className="blog__content">
                {markdown ? <ReactMarkdown children={markdown} allowDangerousHtml /> : <p>loading...</p>}
            </div>
            <div className="blog__tags">
                {tags && tags.map((tag, index) => (
                    <a key={index} className="blog__tag" href={`/blog/tags/${tag}`}>{tag}</a>
                ))}
            </div>
        </section>
    )
}

export default SingleBlog
