import React, { 
    useContext, 
    // useEffect, 
    useState 
} from 'react'
import initialSite from '../data.json';

const DataContext = React.createContext()

export function useData() {
    return useContext(DataContext);
}

export function DataProvider({children}) {
    const [loading, setLoading] = useState(true);
    const [site, setSite] = useState(initialSite);
    const blogData = [
        {
            "slug": "building-a-react-folder-tree-component",
            "title": "Building A React Folder Tree Component",
            "date": "2020-04-29 18:39:00",
            "readtime": "1",
            "author": "Sanjay Goraniya",
            "tags": ['reactjs', 'javascript'],
            "content": "Content for building a react folder tree component."
        }
    ]

    const value = {
        loading,
        setLoading,
        site,
        setSite,
        blogData
    }

    return (
        <DataContext.Provider value={value}>
            {children}
        </DataContext.Provider>
    )
}