import React from 'react';
import { useData } from '../contexts/index';
import {
    Intro,
    Background,
    Skills,
    Experience,
    OtherProjects
} from '../components';
import { Page } from './';

const LandingPage = (props) => {
    const { site } = useData();

    React.useEffect(() => {
        if(props.location.hash && props.location.hash.startsWith('#')) {
            let elem = document.body.querySelector(props.location.hash);
            if(elem) {
                elem.scrollIntoView({ behavior: 'smooth', block: 'start' })
            }
        }
    }, [props.location.hash])    

    return (
        <Page>
            <Intro email={`mailto:${site.email}`} />
            <Background email={`mailto:${site.email}`} />
            <Skills skills={site.data.skills} />
            <Experience experience={site.data.experience} />
            {/* Featured Projects */}
            <OtherProjects otherProjects={site.data.otherProjects} />
        </Page>
    )
}

export default LandingPage;